var _this = this;

var request = require('request');

var util = require('./util');

var basePath = 'v1/auth/login';

exports.do = function (ethAddress, password, apiUrl) {
  return new Promise(function (resolve, reject) {
    var options = {
      form: {
        ethAddress: ethAddress,
        password: password
      }
    };
    var url = util.joinUrl(apiUrl.href, basePath);
    request.post(url, options, function (error, res, body) {
      if (error) {
        reject(error);
        return;
      } // Handle redirect


      if (res.statusCode === 308 && apiUrl.protocol === 'http:') {
        apiUrl.protocol = 'https:';

        _this.do(ethAddress, password, apiUrl).then(function (result) {
          resolve(result);
        }).catch(function (error) {
          reject(error);
        });

        return;
      }
      /* eslint-disable prefer-promise-reject-errors */


      if (res.statusCode !== 200) {
        try {
          body = JSON.parse(body);
          reject("Parse error of JWT login request: ".concat(body.error, " (HTTP status ").concat(res.statusCode, ")"));
        } catch (err) {
          reject("JSON result parse error for JWT login; body: ".concat(err, " (HTTP status ").concat(res.statusCode, ")"));
        }

        return;
      }

      try {
        body = JSON.parse(body);
      } catch (err) {
        reject("JSON result parse error for JWT login; error: ".concat(err));
        return;
      }

      if (!body.refresh) {
        reject("JWT login refresh token not received.");
      }

      if (!body.access) {
        reject("JWT login access token not received.");
      }
      /* eslint-enable prefer-promise-reject-errors */


      resolve(body);
    });
  });
};