/*
   This is somewhat generic but specific to the MythX API and handles
   API requests which do not require more than a single request.

   This is in contrast, say, to analysis where a request, needs
   to be followed by polling.
*/
var request = require('request');

var HttpErrors = require('http-errors');

var trialEthAccount = '0x0000000000000000000000000000000000000000';

exports.do = function (options) {
  return new Promise(function (resolve, reject) {
    var reqOptions = {
      url: options.url
    };

    if (options.accessToken) {
      reqOptions.headers = {
        'Authorization': "Bearer ".concat(options.accessToken)
      };
    }

    request(reqOptions, function (error, res, body) {
      if (error) {
        reject(error);
        return;
      }

      if (res.statusCode === 401) {
        var prefix = 'HTTP status 401: ';

        try {
          body = JSON.parse(body);
        } catch (err) {
          reject(new Error("".concat(prefix).concat(err)));
          return;
        }

        reject(HttpErrors(res.statusCode, body.error));
        return;
      } else if (res.statusCode === 403 && res.request.uri.path.endsWith('/analyses') && options.ethAddress === trialEthAccount) {
        // FIXME: Remove when API gives back a custom message like this:
        // eslint-disable-next-line prefer-promise-reject-errors
        reject('The trial user does not allow listing previous analyses. To enable this feature, please sign up for a free account on: https://mythx.io');
      } else if (res.statusCode !== 200) {
        try {
          body = JSON.parse(body);
        } catch (err) {
          reject(new Error("JSON parse error ".concat(err)));
          return;
        }

        reject(HttpErrors(res.statusCode, 'Invalid status code, expected 200'));
        return;
      }

      if (options.json) {
        try {
          body = JSON.parse(body);
        } catch (err) {
          reject(new Error("JSON parse error ".concat(err)));
          return;
        }
      }

      resolve(body);
    });
  });
};